/* Custom CSS for the Ethereal Offering project */
:root {
  --nav-height: 80px; /* Height of fixed navigation bar */
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to bottom, #581c87, #000000, #000000);
  color: white;
  overflow-x: hidden; /* Prevent horizontal scroll globally */
  max-width: 100vw;
  min-height: 100vh;
}

input, textarea, select {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Breathing Shadow Effects - Opt-in with CSS classes */
/* Slower, more subtle breathing - 12s duration instead of 6s */
@keyframes breathingShadowSubtle {
  0%, 100% {
    filter: drop-shadow(0 2px 4px rgba(168, 85, 247, 0.4));
  }
  50% {
    filter: drop-shadow(0 2px 6px rgba(168, 85, 247, 0.6));
  }
}

@keyframes breathingShadowGold {
  0%, 100% {
    filter: drop-shadow(0 1px 3px rgba(251, 191, 36, 0.3));
  }
  50% {
    filter: drop-shadow(0 2px 4px rgba(251, 191, 36, 0.5));
  }
}

@keyframes breathingShadowText {
  0%, 100% {
    filter: drop-shadow(0 1px 2px rgba(168, 85, 247, 0.2));
  }
  50% {
    filter: drop-shadow(0 1px 3px rgba(168, 85, 247, 0.35));
  }
}

/* Opt-in classes - only apply when lightweight mode is OFF */
body:not(.lightweight-mode) .breathing-shadow-title {
  animation: breathingShadowSubtle 12s ease-in-out infinite;
}

body:not(.lightweight-mode) .breathing-shadow-gold {
  animation: breathingShadowGold 12s ease-in-out infinite;
}

body:not(.lightweight-mode) .breathing-shadow-text {
  animation: breathingShadowText 12s ease-in-out infinite;
}

/* Bounce animation for chevrons */
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

/* Subtle bounce animation for modal buttons */
@keyframes subtleBounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-3px);
  }
}

/* Modal button hover bounce effect */
.modal-button-bounce {
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.modal-button-bounce:hover {
  animation: subtleBounce 0.6s ease-in-out;
}

/* Landing Page Styles */
/* Padding-top set dynamically by DynamicPagePadding component */
.landing-container {
  min-height: 100vh;
  max-width: 100vw; /* Prevent horizontal scroll */
  overflow-x: hidden; /* Hide horizontal overflow */
  background: linear-gradient(to bottom, #581c87, #000000, #000000);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Changed from center to prevent navbar overlap */
  padding: 24px;
  padding-top: 0; /* Top spacing is handled dynamically via --top-padding on containers */
}

.hero-section {
  text-align: center;
  max-width: 672px;
  margin-bottom: 40px;
}

.hero-title {
  font-size: 4.5rem;
  font-weight: bold;
  margin-bottom: 24px;
  background: linear-gradient(90deg, #f472b6, #fbbf24, #a78bfa, #ec4899, #f472b6);
  background-size: 300% 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradientShift 4s ease infinite;
  letter-spacing: 2px;
}

.hero-subtitle {
  font-size: 1.125rem;
  color: #d1d5db;
  margin-bottom: 24px;
}

.button-group {
  display: flex;
  gap: 16px;
  justify-content: center;
  flex-wrap: wrap;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  font-weight: 500;
  transition: all 0.2s;
  cursor: pointer;
  border: none;
  text-decoration: none;
  animation: buttonBreathing 2.5s ease-in-out infinite;
}

.btn:hover {
  animation: none; /* Pause breathing on hover */
}

.btn-lg {
  padding: 12px 24px;
  font-size: 1.125rem;
}

.btn-primary {
  background-color: #7c3aed;
  color: white;
}

.btn-primary:hover {
  background-color: #6d28d9;
}

.btn-secondary {
  background-color: #4b5563;
  color: white;
}

.btn-secondary:hover {
  background-color: #374151;
}

.btn-outline {
  border: 1px solid #7c3aed;
  color: #7c3aed;
  background: transparent;
}

.btn-outline:hover {
  background-color: #7c3aed;
  color: white;
}

.wallet-section {
  margin: 40px 0;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin-top: 64px;
  max-width: 1280px;
  width: 100%;
}

.feature-card {
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid #7c3aed;
  border-radius: 20px;
  padding: 28px;
  transition: all 0.3s ease;
  animation: cardBreathing 3s ease-in-out infinite;
  box-shadow: 0 4px 20px rgba(124, 58, 237, 0.3), inset 0 0 20px rgba(124, 58, 237, 0.1);
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}

.feature-card:hover {
  transform: scale(1.05);
  animation: none; /* Pause breathing on hover */
  border-color: #ec4899;
  box-shadow: 0 8px 30px rgba(236, 72, 153, 0.5), inset 0 0 30px rgba(236, 72, 153, 0.2);
}

.feature-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 8px;
}

/* Apply breathing shadow to feature titles when not in lightweight mode */
body:not(.lightweight-mode) .feature-title {
  animation: breathingShadowGold 12s ease-in-out infinite;
}

.feature-description {
  color: #d1d5db;
  font-size: 0.875rem;
}

/* Apply subtle breathing shadow to feature descriptions when not in lightweight mode */
body:not(.lightweight-mode) .feature-description {
  animation: breathingShadowText 12s ease-in-out infinite;
}

.footer {
  margin-top: 80px;
  color: #6b7280;
  font-size: 0.875rem;
  animation: footerBreathing 4s ease-in-out infinite;
}

/* Equal-height cards across grids */
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, 320px), 1fr));
  gap: 24px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}

.features-grid > * {
  display: flex;
  width: 100%;
}

.feature-card {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 300px;
  width: 100%;
  box-sizing: border-box;
}

.feature-card > div,
.feature-card > .card-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
}

/* Page content container - dynamic padding for navbar + welcome message */
/* Padding set dynamically by DynamicPagePadding component */
.page-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px; /* Top spacing is provided by container via --top-padding */
  width: 100%;
  box-sizing: border-box;
  transition: padding-top 0.3s ease;
}

/* Content sections */
.content-section {
  max-width: 1200px;
  animation: cardBreathing 3.5s ease-in-out infinite;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  box-sizing: border-box;
}

@media (max-width: 1024px) {
  .features-grid {
    grid-template-columns: repeat(auto-fit, minmax(min(100%, 280px), 1fr));
    gap: 20px;
  }
}

@media (max-width: 768px) {
  .features-grid {
    grid-template-columns: 1fr;
    gap: 16px;
    padding: 0 16px;
  }

  .page-content {
    padding: 16px;
  }

  .content-section {
    padding: 0 16px;
  }

  .feature-card {
    min-height: 250px;
  }
}

@media (max-width: 480px) {
  .features-grid {
    padding: 0 12px;
  }

  .page-content {
    padding: 12px;
  }
}

/* Admin link pulse animation */
@keyframes pulse {
  0%, 100% {
    box-shadow: 0 0 15px rgba(251,191,36,0.4);
  }
  50% {
    box-shadow: 0 0 25px rgba(251,191,36,0.7);
  }
}

/* Sparkle animation */
@keyframes sparkle {
  0%, 100% {
    opacity: 0.9;
    transform: scale(1) rotate(0deg);
  }
  50% {
    opacity: 1;
    transform: scale(1.2) rotate(180deg);
  }
}

/* Wavy psychedelic effect */
@keyframes wave {
  0%, 100% {
    transform: translateY(0) scale(1);
  }
  25% {
    transform: translateY(-2px) scale(1.002);
  }
  50% {
    transform: translateY(0) scale(1);
  }
  75% {
    transform: translateY(2px) scale(0.998);
  }
}

.wavy-container {
  animation: wave 6s ease-in-out infinite;
}


/* Animation classes */
.fade-in {
  animation: fadeIn 1s ease-in;
}

.fade-in-delay {
  animation: fadeIn 1s ease-in 0.5s both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Navigation Styles */
.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(124, 58, 237, 0.3);
  animation: navBreathing 4s ease-in-out infinite;
}

@keyframes navBreathing {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 4px 6px rgba(124, 58, 237, 0.1);
  }
  50% {
    transform: scale(1.005);
    box-shadow: 0 6px 12px rgba(124, 58, 237, 0.2);
  }
}

@keyframes iconRocking {
  0%, 100% {
    transform: rotate(-3deg);
  }
  50% {
    transform: rotate(3deg);
  }
}

@keyframes logoSpin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes logoFlip {
  0% { transform: rotateY(0deg); }
  50% { transform: rotateY(180deg); }
  100% { transform: rotateY(360deg); }
}

@keyframes logoSpinFlip {
  0% { transform: rotate(0deg) rotateY(0deg); }
  25% { transform: rotate(90deg) rotateY(90deg); }
  50% { transform: rotate(180deg) rotateY(180deg); }
  75% { transform: rotate(270deg) rotateY(270deg); }
  100% { transform: rotate(360deg) rotateY(360deg); }
}

@keyframes logoSlowFlip {
  0%, 100% { transform: rotateY(0deg) scale(1); }
  50% { transform: rotateY(180deg) scale(1.1); }
}

@keyframes backgroundBreathing {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.03);
    opacity: 0.92;
  }
}

@keyframes cardBreathing {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 4px 6px rgba(124, 58, 237, 0.1);
  }
  50% {
    transform: scale(1.01);
    box-shadow: 0 6px 12px rgba(124, 58, 237, 0.2);
  }
}

@keyframes buttonBreathing {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
}

@keyframes footerBreathing {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.015);
    opacity: 0.95;
  }
}

@keyframes faqBreathing {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 4px 12px rgba(124, 58, 237, 0.2);
  }
  50% {
    transform: scale(1.01);
    box-shadow: 0 6px 16px rgba(124, 58, 237, 0.3);
  }
}

.faq-item {
  animation: faqBreathing 3.5s ease-in-out infinite;
}

.faq-item:hover {
  animation: none;
}

.nav-container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 16px 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Navbar icon rocking animation */
.nav-links a svg,
.nav-logo svg {
  animation: iconRocking 2s ease-in-out infinite;
  transform-origin: center;
}

/* Admin gear icon spinning */
.nav-links a[href="/admin"] svg {
  animation: logoSpin 20s linear infinite;
}

.nav-links a[href="/admin"]:hover svg {
  animation: logoSpin 5s linear infinite;
}

.nav-logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  gap: 12px;
}

.nav-logo-img {
  height: 48px;
  width: 48px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #7c3aed;
  animation: logoSpin 40s linear infinite;
  transition: all 0.6s ease-in-out;
}

.nav-logo:hover .nav-logo-img {
  animation: logoSlowFlip 3s ease-in-out infinite;
  border-color: #ec4899;
  box-shadow: 0 0 20px rgba(236, 72, 153, 0.5);
}

.nav-logo-img:active {
  animation: logoFlip 0.8s ease-in-out;
}

.nav-logo-text {
  font-size: 1.5rem;
  font-weight: bold;
  background: linear-gradient(to right, #f472b6, #fbbf24);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.nav-links-desktop {
  display: flex;
  gap: 8px;
  flex: 1;
  justify-content: center;
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border-radius: 8px;
  text-decoration: none;
  color: #d1d5db;
  transition: all 0.2s;
  font-weight: 500;
}

.nav-link:hover {
  background: rgba(124, 58, 237, 0.2);
  color: white;
}

.nav-link-active {
  background: rgba(124, 58, 237, 0.4);
  color: white;
}

.nav-link-icon {
  font-size: 1.25rem;
  min-width: 22px;
  min-height: 22px;
  flex-shrink: 0;
}

.nav-link-label {
  font-size: 0.875rem;
}

/* Welcome Message Banner - Full Width Below Nav */
.nav-welcome-banner {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 24px;
  background: linear-gradient(90deg, rgba(124, 58, 237, 0.15), rgba(236, 72, 153, 0.15), rgba(124, 58, 237, 0.15));
  border-bottom: 1px solid rgba(124, 58, 237, 0.3);
  backdrop-filter: blur(10px);
  animation: welcomeGlow 3s ease-in-out infinite;
}

@keyframes welcomeGlow {
  0%, 100% {
    box-shadow: 0 2px 20px rgba(124, 58, 237, 0.2);
  }
  50% {
    box-shadow: 0 2px 30px rgba(236, 72, 153, 0.3);
  }
}

.nav-wallet {
  display: flex;
  align-items: center;
}

.nav-mobile-toggle {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 8px;
}

.nav-links-mobile {
  display: none;
  flex-direction: column;
  padding: 16px;
  background: rgba(0, 0, 0, 0.98);
  border-top: 1px solid rgba(124, 58, 237, 0.3);
}

.nav-link-mobile {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  border-radius: 8px;
  text-decoration: none;
  color: #d1d5db;
  transition: all 0.2s;
  font-weight: 500;
}

.nav-link-mobile:hover {
  background: rgba(124, 58, 237, 0.2);
  color: white;
}

/* Page Styles */
/* CRITICAL: Padding set dynamically by DynamicPagePadding component */
/* This ensures navbar never hides content - it pushes content down instead */
.page-container {
  min-height: 100vh;
  padding-bottom: 80px;
  position: relative;
  overflow: hidden;
  /* padding-top set dynamically via JS */
}

/* Unified dynamic top padding for all pages to clear the fixed navbar */
.landing-container,
.page-container {
  padding-top: var(--top-padding, 160px) !important;
}

/* Ensure inner page-content does not add additional top padding */
.page-content { padding-top: 0 !important; }


/* Breathing effect for page backgrounds */
.page-container::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: inherit;
  animation: backgroundBreathing 8s ease-in-out infinite;
  z-index: -1;
}

.page-content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 24px;
}

.page-title {
  font-size: 3.5rem;
  font-weight: bold;
  margin-bottom: 16px;
  background: linear-gradient(90deg, #f472b6, #fbbf24, #a78bfa, #f472b6);
  background-size: 200% 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  animation: gradientShift 4s ease infinite;
}

@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  10%, 30% {
    transform: scale(1.1);
  }
  20%, 40% {
    transform: scale(1);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.page-subtitle {
  font-size: 1.25rem;
  color: #d1d5db;
  text-align: center;
  margin-bottom: 40px;
  font-style: italic;
}

/* Apply breathing shadow to page subtitles when not in lightweight mode */
body:not(.lightweight-mode) .page-subtitle {
  animation: breathingShadowText 12s ease-in-out infinite;
}

.content-section {
  margin-top: 60px;
}

.section-title {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 16px;
  color: #fbbf24;
}

/* Apply breathing shadow to section titles when not in lightweight mode */
body:not(.lightweight-mode) .section-title {
  animation: breathingShadowGold 12s ease-in-out infinite;
}

.section-text {
  font-size: 1.125rem;
  line-height: 1.8;
  color: #d1d5db;
}

/* Responsive design */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2rem;
  }

  .button-group {
    flex-direction: column;
    align-items: center;
  }

  .features-grid {
    grid-template-columns: 1fr;
  }

  .nav-links-desktop {
    display: none;
  }

  .nav-welcome-banner {
    padding: 8px 12px;
    font-size: 0.9rem;
  }

  .nav-wallet {
    display: none;
  }

  .nav-mobile-toggle {
    display: block;
  }

  .nav-links-mobile {
    display: flex;
  }

  .page-title {
    font-size: 2rem;
  }

  .page-subtitle {
    font-size: 1rem;
  }
}

/* Info Box Styles - enhanced glassmorphism */
.info-box {
  background: rgba(124, 58, 237, 0.15);
  border: 1px solid rgba(124, 58, 237, 0.3);
  border-radius: 12px;
  padding: 24px;
  margin-top: 20px;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
}

/* Smaller cards and info tips - lighter glassmorphism for visual hierarchy */
.info-tip,
.small-card,
.mushroom-card,
.gallery-item,
.oracle-agent-card,
.token-info-item {
  backdrop-filter: blur(4px) !important;
  -webkit-backdrop-filter: blur(4px) !important;
}

.info-box h3 {
  color: #fbbf24;
  margin-top: 0;
}

.info-box ol, .info-box ul {
  color: #d1d5db;
}

/* Traits Grid */
.traits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
  margin-top: 20px;
}

.trait-box {
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: 8px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.trait-label {
  font-weight: 600;
  color: #a78bfa;
  font-size: 0.875rem;
}

.trait-value {
  color: #d1d5db;
  font-size: 0.875rem;
}

/* Breeding Examples */
.breeding-examples {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.breeding-card {
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: 12px;
  padding: 20px;
}

.breeding-formula {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  flex-wrap: wrap;
  margin-bottom: 12px;
}

.parent, .offspring {
  padding: 8px 12px;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.875rem;
}

.parent {
  background: rgba(251, 146, 60, 0.2);
  color: #fb923c;
}

.offspring {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
}

.plus, .equals {
  color: #9ca3af;
  font-weight: bold;
}

.breeding-desc {
  text-align: center;
  color: #d1d5db;
  font-size: 0.875rem;
  margin: 0;
}

/* Principles Grid */
.principles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.principle-card {
  background: rgba(124, 58, 237, 0.1);
  border: 1px solid rgba(124, 58, 237, 0.3);
  border-radius: 12px;
  padding: 20px;
}

.principle-card h3 {
  color: #fbbf24;
  margin-top: 0;
  margin-bottom: 12px;
  font-size: 1.125rem;
}

/* Apply breathing shadow to principle card headings when not in lightweight mode */
body:not(.lightweight-mode) .principle-card h3 {
  animation: breathingShadowGold 12s ease-in-out infinite;
}

