/* Psychedelic Background Animation */
@keyframes psychedelic-gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.psychedelic-bg {
  background: linear-gradient(
    -45deg,
    #581c87,
    #7c3aed,
    #ec4899,
    #f59e0b,
    #10b981,
    #3b82f6,
    #8b5cf6
  );
  background-size: 400% 400%;
  animation: psychedelic-gradient 15s ease infinite;
}



/* Rainbow pseudonym wave per-letter */
.rainbow-pseudonym .rainbow-letter {
  display: inline-block;
  background: linear-gradient(90deg, #f472b6, #fbbf24, #22c55e, #3b82f6, #8b5cf6, #ec4899, #f472b6);
  background-size: 300% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: rainbowShift 6s linear infinite;
}
@keyframes rainbowShift {
  0% { background-position: 0% 50%; }
  100% { background-position: 100% 50%; }
}

/* Sparkly animated border for CTAs and highlight boxes */
.sparkly-border {
  position: relative;
  border-radius: 12px;
  background: linear-gradient(135deg, rgba(124,58,237,0.15), rgba(236,72,153,0.15));
  border: 2px solid transparent;
}
.sparkly-border::before {
  content: "";
  position: absolute;
  inset: -2px;
  border-radius: inherit;
  padding: 2px;
  background: conic-gradient(from 0deg, #fbbf24, #ec4899, #7c3aed, #a78bfa, #fbbf24);
  -webkit-mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
          mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  -webkit-mask-composite: xor;
          mask-composite: exclude;
  animation: sparklyBorderSpin 6s linear infinite;
}
@keyframes sparklyBorderSpin {
  to { transform: rotate(360deg); }
}

/* Extra sparkle glints */
.sparkly-border::after {
  content: "";
  position: absolute;
  width: 8px; height: 8px;
  right: 8px; top: 8px;
  background: radial-gradient(circle, #fff, rgba(255,255,255,0));
  border-radius: 50%;
  filter: drop-shadow(0 0 8px rgba(251,191,36,0.8));
  animation: sparkle 2.4s ease-in-out infinite;
}

/* Gentle page-wide breathing for watermark */
@keyframes breathPage {
  0%, 100% { transform: scale(0.92); }
  50% { transform: scale(1.06); }
}

/* Watermark breathing - lungs-like expansion */
@keyframes watermarkBreath {
  0%, 100% { transform: scale(0.85); }
  50% { transform: scale(1.35); }
}

/* Watermark heartbeat - subtle glow pulse synced to navbar */
@keyframes watermarkHeartbeat {
  0%, 100% { filter: drop-shadow(0 0 40px rgba(124, 58, 237, 0.6)) drop-shadow(0 0 80px rgba(236, 72, 153, 0.4)); }
  10%, 30% { filter: drop-shadow(0 0 50px rgba(124, 58, 237, 0.75)) drop-shadow(0 0 100px rgba(236, 72, 153, 0.55)); }
  20%, 40% { filter: drop-shadow(0 0 40px rgba(124, 58, 237, 0.6)) drop-shadow(0 0 80px rgba(236, 72, 153, 0.4)); }
}

/* Global heartbeat animation - used across all pages */
@keyframes heartbeat {
  0%, 100% { transform: scale(1); }
  10% { transform: scale(1.1); }
  20% { transform: scale(1); }
  30% { transform: scale(1.1); }
  40% { transform: scale(1); }
}

/* Wallet connect button and icon heartbeat - synced to navbar - FORCE ON ALL PAGES */
.wallet-connect-btn,
.wallet-icon-heartbeat,
.nav-links a svg,
.nav-logo svg,
.nav-icon-heartbeat,
.nav-link-icon,
nav a svg,
.navigation a svg,
.nav-link svg,
.force-heartbeat,
.nav-admin-icon {
