  animation-timing-function: ease-in-out !important;
  animation-iteration-count: infinite !important;
  animation-fill-mode: both !important;
}

/* Strengthen specificity so Daily Shrooms and Cross-Breeding icons beat identically - ULTRA SPECIFIC */
.navigation .nav-links a .nav-link-icon,
.navigation .nav-links-desktop .nav-link .nav-link-icon,
.navigation .nav-links-desktop .nav-link .force-heartbeat,
.navigation .nav-links a[href="/daily-shrooms"] .nav-link-icon,
.navigation .nav-links a[href="/cross-breeding"] .nav-link-icon,
.navigation .nav-links-desktop a[href="/daily-shrooms"] .nav-link-icon,
.navigation .nav-links-desktop a[href="/cross-breeding"] .nav-link-icon,
.navigation a[href="/daily-shrooms"] .force-heartbeat .nav-link-icon,
.navigation a[href="/cross-breeding"] .force-heartbeat .nav-link-icon,
.navigation a[href="/daily-shrooms"] span .nav-link-icon,
.navigation a[href="/cross-breeding"] span .nav-link-icon {
