}

/* Static sparkly border for Oracle and CTAs - no rotation */
.oracle-sparkly {
  position: relative;
  border-radius: 12px;
  background: linear-gradient(135deg, rgba(124,58,237,0.15), rgba(236,72,153,0.15));
  border: 2px solid transparent;
  background-clip: padding-box;
}
.oracle-sparkly::before {
  content: "";
  position: absolute;
  inset: -3px;
  border-radius: inherit;
  padding: 3px;
  background: linear-gradient(90deg, #fbbf24 0%, #ec4899 25%, #7c3aed 50%, #a78bfa 75%, #fbbf24 100%);
  -webkit-mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
          mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  -webkit-mask-composite: xor;
          mask-composite: exclude;
  pointer-events: none;
  animation: sparkleFlip 4s ease-in-out infinite;
}

@keyframes sparkleFlip {
  0%, 100% { transform: scaleY(1); }
  50% { transform: scaleY(-1); }
}

/* Contact card shimmer border - subtle traveling particles */
/* Mimics mushroom gallery placeholder effect */
.contact-card-shimmer {
  position: relative;
  border-radius: 16px;
  background: rgba(124,58,237,0.15);
  backdrop-filter: blur(12px);
  border: 2px solid rgba(124, 58, 237, 0.4);
  overflow: visible !important;
}

/* Contact card content breathing animation */
.contact-card-content {
  animation: gentleBreathing 4s ease-in-out infinite;
}

@keyframes gentleBreathing {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.02); }
}

@keyframes sparkleRotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Contact icon animations */
.contact-icon-animated {
  animation: heartbeat 2s ease-in-out infinite, iconRocking 3s ease-in-out infinite;
}

/* Contact card title styling - stand out from background with heartbeat */
.contact-card-title {
  font-size: 1.5rem;
  font-weight: 700;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.8), 0 0 20px currentColor;
  letter-spacing: 0.5px;
  margin: 12px 0;
  animation: heartbeat 2s ease-in-out infinite;
}

/* Custom spinner for Suspense fallback */
.custom-spinner {
  display: inline-block;
  width: 3.0rem;
  height: 3.0rem;
  border: 5.0px solid rgba(124, 58, 237, .2);
  border-top-color: #a78bfa;
  border-bottom-color: #e48bfa;
  border-right-color: #fbbf24;
  border-left-color: #ec4899;
  border-radius: 50%;
  animation: spin 1s ease-in-out infinite;
  /*animation: pulse 1.5s ease-out infinite; /* Reference the new animation */
  box-shadow: 0 0 33px rgba(236, 72, 153, 0.5); /* Add a glow effect */
  margin-left: 10px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Define the new pulsing keyframes */
/*
@keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  50% {
    transform: scale(1);
    opacity: 0.5;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}
*/

.custom-spinner-dots {
  position: relative;
  width: 2rem;
  height: 2rem;
}

.custom-spinner-dots::before,
.custom-spinner-dots::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: #a78bfa;
  transform: translate(-50%, -50%);
  animation: dot-spin 2s cubic-bezier(0.7, 0, 0.3, 1) infinite;
}

.custom-spinner-dots::after {
  background-color: #ec4899;
  animation-delay: 1s; /* Start the second dot halfway through */
}

@keyframes dot-spin {
  0%, 100% {
    transform: translate(-50%, -50%) scale(0);
  }
  50% {
    transform: translate(-50%, -50%) scale(1);
  }
}

/* Hide scrollbar utility class */
.hide-scrollbar {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.hide-scrollbar::-webkit-scrollbar {
  display: none;  /* Chrome, Safari and Opera */
}

/* Blockchain cubes breathing animation - toggleable with lightweight mode */
@keyframes cubesBreathing {
  0%, 100% {
    filter: drop-shadow(0 0 8px rgba(124, 58, 237, 0.3)) drop-shadow(0 0 16px rgba(236, 72, 153, 0.2));
    opacity: 0.85;
  }
  50% {
    filter: drop-shadow(0 0 12px rgba(124, 58, 237, 0.5)) drop-shadow(0 0 24px rgba(236, 72, 153, 0.35));
    opacity: 1;
  }
}

.blockchain-cubes-breathing {
  animation: cubesBreathing 6s ease-in-out infinite;
}